import * as axios from "axios";

export const apiFirebase = axios.create({
  baseURL: "https://movies-ce67c-default-rtdb.europe-west1.firebasedatabase.app/",
});

export default {
  fetchFavorites:  () => apiFirebase
                          .get("favorites.json")
                          .then( (response) => response.data ? response.data : [] ),
  
  saveFavorites: favorites => apiFirebase
                                  .put("favorites.json", favorites)
                                  .then()
}


