import * as types from './movies.actions';

export default (state = {
    data: [],
    isLoading: false,
    error: null,
    selectedMovie :0
}, action) => {

    switch (action.type) {
      case types.SEARCH_MOVIES: {
        return {
          ...state,
          isLoading: true,
        };
      }
      case types.FETCH_MOVIES_SUCCESS: {
        return {
          ...state,
          data: [ ...action.movies ],
          isLoading: false,
          error: null
        };
      }
      case types.FETCH_MOVIES_ERROR: {
        return {
          ...state,
          isLoading: false,
          error: action.error
        };
      }
      case types.SET_SELECTED_MOVIE: {
        return {
          ...state,
          selectedMovie: action.index
        };
      }
      default : {
          return state;
      }
    }
}


