import * as types from './favorites.actions';

export default (state = {
    data: [],
    isLoading: false,
    error: null
}, action) => {

    switch (action.type) {
      case types.GET_FAVORITES: {
        return {
          ...state,
          isLoading: true,
          error: null,
        };
      }
      case types.FETCH_FAVORITES_SUCCESS: {
        return {
          ...state,
          data: action.favorites,
          isLoading: false,
          error: null,
        };
      }
      case types.FETCH_FAVORITES_ERROR: {
        return {
          ...state,
          isLoading: false,
          error: action.error,
        };
      }

      case types.ADD_FAVORITE_SUCCESS:
      case types.REMOVE_FAVORITE_SUCCESS: {
        return {
          ...state,
          data: action.favorites,
          isLoading: false,
          error: null,
        };
      }
      case types.ADD_FAVORITE_ERROR:
      case types.REMOVE_FAVORITE_ERROR: {
        return {
          ...state,
          isLoading: false,
          error: action.error,
        };
      }

      default: {
        return state;
      }
    }
    

};