export const apiMovieMap = (item) => ({
  id: item.id,
  title: item.title,
  img: item.poster_path && item.poster_path != "" ? `https://image.tmdb.org/t/p/w500/${item.poster_path}` : noImage,
  details: ` ${item.release_date} | ${item.vote_average}/10`,
  description: item.overview,
});

export default {
  getMovies: ( queryObj ) => {

    const { query, ...fields } = queryObj;
    const queryStr = 
      (( query && query.length >0 ) ? '/search/movie' : '/discover/movie')
      + '?'
      + Object.keys(queryObj)
          .map((item) => `${item}=${queryObj[item]}`)
          .join('&');

    console.log(queryStr);

    return apiMovie
      .get(queryStr)
      .then((response) => response.data.results)
      .then((moviesList) => moviesList.map(apiMovieMap));
  }
};
