import React, { Component, lazy, Suspense } from "react";
import { Header } from "./components";
import { Routes, Route, Navigate } from "react-router-dom";
import { fetchMovies, fetchFavorites } from "./store/actions";
import { connect } from "react-redux";

const LazyPageMovies = lazy(() => import(/*webpackChunkName: "PageMovies"*/ "./features/movies"));
const LazyPageFavorites = lazy(() => import(/*webpackChunkName: "PageFavorites"*/ "./features/favorites"));

class App extends Component {
  componentDidMount = () => {
    // this.getMovies("");
    this.props.fetchMovies('');
    this.props.fetchFavorites();
  };

  render() {
    return (
      <div className="App d-flex flex-column">
        <Header />
        <Suspense fallback={<h1>Loading ...</h1>}>
          <Routes>
            <Route path="/movies" element={<LazyPageMovies />} />
            <Route path="/favorites" element={<LazyPageFavorites />} />
            <Route path="*" element={<Navigate to="/movies" />} />
          </Routes>
        </Suspense>
      </div>
    );
  }
}

export default connect(null, {
  fetchMovies,
  fetchFavorites,
})(App);
